import React from "react"
import "basscss/css/basscss.min.css"
import Dialog from "./dialog"
import Background from "./background"

import "./layout.css"
class Layout extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      DialogVis: false,
      DialogCon: "",
      form: {
        searchVal: "",
      },
      searchOptions: [
        {
          name: "百度",
          tag: "baidu",
        },
        {
          name: "必应",
          tag: "bing",
        },
        {
          name: "谷歌",
          tag: "google",
        },
        {
          name: "匿名",
          tag: "duckduckgo",
        },
      ],
    }
  }
  handleChange(event) {
    this.setState({
      form: Object.assign({}, this.state.form, { [event.target.name]: event.target.value }),
    })
  }
  handleSearch(searchVal) {
    const { form } = this.state
    if (form.searchVal == "") return false
    switch (searchVal) {
      case "baidu":
        window.open(`https://www.baidu.com/s?wd=${form.searchVal}`)
        break
      case "bing":
        window.open(`https://www.bing.com/search?q=${form.searchVal}`)
        break
      case "google":
        window.open(`https://www.google.com/search?q=${form.searchVal}`)
        break
      case "duckduckgo":
        window.open(`https://www.duckduckgo.com/?q=${form.searchVal}`)
        break
      default:
        break
    }
  }
  handleOpen() {
    this.setState({
      DialogVis: true,
      DialogCon: '',
    })
  }
  handleCancel() {
    this.setState({
      DialogVis: false,
    })
  }
  render() {
    const { DialogVis, DialogCon, form, searchOptions } = this.state
    return (
      <>
        <header className="clearfix">
          <div className="sm-col sm-col-12 md-col-12 lg-col-12 pt1 pb1 px1 md-px3">
            <div className="clearfix head-box elevation">
              <div className="sm-col sm-col-12 md-col-12 lg-col-8 px1 md-px3">
                <h1 className="head-logo">Sroot-Nav</h1>
                <p className="head-des">一个专注互联网DevOps知识导航</p>
              </div>
              <div className="sm-col sm-col-12 md-col-12 lg-col-4 px1 md-px3">
                <div className="search-box">
                  <div className="saerch-input">
                    <input placeholder="搜索" value={form.searchVal} name="searchVal" onChange={this.handleChange.bind(this)} />
                  </div>
                  <div className="right-align">
                    {searchOptions.map((item, index) => (
                      <button key={index} className={`search-btn ${item.tag}`} onClick={this.handleSearch.bind(this, item.tag)}>
                        {item.name}
                      </button>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </header>
        <main>{this.props.children}</main>
        <footer className="p2 center">
          <div className="pt1 pb1 md-px3">
            <div className="pt1 pb1">
              <span className="h3 px1">Powered by</span>
              <a href="https://www.gatsbyjs.org" target="_blank">
                <img className="icon-powered" src={"./gatsbyjs.svg"} />
              </a>
            </div>
          </div>
          <p>
            ©2016-{new Date().getFullYear()} <span className="bold">Sroot</span>
          </p>
        </footer>
        <Background />
        <Dialog isShow={DialogVis} content={DialogCon} onCancel={this.handleCancel.bind(this)} />
      </>
    )
  }
}
export default Layout
